@import "../../inc/vars";
.acrobits_softphone-steps-form {
	padding: 50px 30px 0;
	text-align: center;
	position: relative;
	min-height: 100vh;
	img {
		max-width: 100%;
		height: auto;
		vertical-align: top;
	}
	&:before {
		content: '';
		display: block;
		width: 447.1745px;
		height: 447.1745px;
		flex-shrink: 0;
		border-radius:  0 447.1745px 0 0 ;
		opacity: 0.1;
		background: var(--Gradients-3, linear-gradient(180deg, #B1F390 0%, #48D5C1 100.25%));
		filter: blur(36.51726150512695px);
		position: absolute;
		left: 0;
		bottom: 0;
		top: auto;
		z-index: -2;
	}
	&:after {
		content: "";
		display: block;
		width: 447.1745px; /* Half of the original width */
		height: 447.1745px; /* Half of the original height */
		flex-shrink: 0;
		border-radius: 447.1745px 0 0 0; /* Rounded top-left corner */
		opacity: .1;
		background: var(--Gradients-01, linear-gradient(180deg, #7048c7 0%, #bd48c7 100%));
		filter: blur(77.3499984741px);
		position: absolute;
		left: auto;
		right: 0;
		bottom: 0;
		z-index: -2;
	}
	@media (min-width: $screen-sm) {
		padding: 60px 30px 0;
		&:before {
			left: 0;
			bottom: 0;
			top: auto;
			//transform: translate(-50%, 50%);
		}
		&:after {
			left: auto;
			right: 0;
			bottom: 0;
			//transform: translate(50%, 50%);
		}
	}
	@media (min-width: $screen-lg) {
		padding: 80px 30px 0;
		max-height: 100vh;
	}
	.step-1, 
	.step-2, 
	.step-3, 
	.step-4, 
	.step-5, 
	.step-6, 
	.step-7 {
		display: none;
	} 
	&__logo {
		display: none;
		@media (min-width: $screen-sm) {
			display: inline-block;
		}
		&-mobile {
			@media (min-width: $screen-sm) {
				display: none;
			}
		}
	}
	.afterlogoText {
		color: #148D65;
		text-align: center;
		@include fig(400, 20, 1.24);
		letter-spacing: -0.02em;
		justify-content: center;
		align-items: center;
		margin-top: 65px;
		@media (min-width: $screen-sm) {
			font-weight: 500;
			font-size: 21px;
			margin-top: 27px;
		}
		&.step-6 {
			@media (max-width: $screen-sm) {
				display: none !important;
			}
		}
	}
	.steps-wrapper {
		display: flex;
		justify-content: center;
	}
	.steps {
		margin: 14px 0;
		position: relative;
		&:before {
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 2px;
			background: #ECEDEE;
			top: calc(50% - 1px);
		}
		@media (min-width: $screen-sm) {
			margin: 36px 0 18px;
		}
		@media (min-width: $screen-lg) {
			margin: 50px 0 38px;
		}
		display: flex;
		justify-content: center;
		gap: 24px;
		align-items: center;
		@media (min-width: $screen-sm) {
			gap: 68px;
		}
		.step {
			border-radius: 50%;
			width: 8px;
			height: 8px;
			background: #ECEDEE;
			@include fig(700, 12, 1.24);
			font-size: 0;
			color: #C7C9CB;
			display: flex;
			justify-content: center;
			align-items: center;
			position: relative;
			&:before, &:after {
				content: '';
				display: block;
				height: 2px;
				width: 12px;
				position: absolute;
				left: 100%;
				top: calc(50% - 1px);
				background: #ECEDEE;
				@media (min-width: $screen-sm) {
					width: 34px;
					left: calc(100% + 2px);
				}
			}
			&:after {
				left: -12px;
				@media (min-width: $screen-sm) {
					left: -36px;
				}
			}
			@media (min-width: $screen-sm) {
				width: 30px;
				height: 30px;
				background: #fff;
				border: 2px solid var(--gray-gray-100, #ECEDEE);
				font-size: 12px;
			}
			&.active {
				background: #1CA87A;
				font-size: 12px;
				color: #fff;
				border-color: #1CA87A;
				width: 30px;
				height: 30px;
				&:before, &:after {
					background: #1CA87A;
				}
				&:before {
					animation: acroGrowHorizontalMobile 0.5s ease-in-out;
					@media (min-width: $screen-sm) {
						animation: acroGrowHorizontal 0.5s ease-in-out;
					}
				}
			}
			&:last-child {
				&:before {
					display: none;
				}
			}
			&:first-child {
				&:after {
					display: none;
				}
			}
		}
	}
	.title-box {
		flex-direction: column;
		margin: 0 auto 24px auto;
		border-radius: 7.069px;
		width: auto;
		padding: 10px 12px;
		background: var(--grad-linkup, linear-gradient(180deg, #DEF8EF 0%, #FFF 100%));
		@media (min-width: $screen-sm) {
			margin: 0 auto 33px auto;
			background: none;
		}
		.title {
			@include fig(500, 15, normal);
			color: #000;
			display: none;
			@media (min-width: $screen-sm) {
				margin-bottom: 7px;
				font-size: 20px;
				font-weight: 400;
				line-height: 1.24;
				letter-spacing: 0.15px;
				display: block;
			}
		}
		.subtitle {
			@include fig(500, 15, normal);
			color: #635E6E;
			margin: 0 auto;
			b, strong {
				color: #000;
				font-weight: 500;
			}
			@media (min-width: $screen-sm) {
				padding: 9px 10px;
				border-radius: 7px;
				background: var(--grad-linkup, linear-gradient(180deg, #DEF8EF 0%, #FFF 100%));
				letter-spacing: 0.15px;
			}
		}
	}
	&__form {
		margin: 0 auto;
		width: 100%;
		max-width: 1400px;
		.gform_wrapper.gravity-theme .gform_validation_errors {
			padding: 16px;
		}
		.gform_wrapper.gravity-theme .gform_validation_errors, .gfield_validation_message {
			display: inline-block;
			width: auto !important;
			background: none !important;
			border: none !important;
			box-shadow: none !important;
		}
		.gform_required_legend {
			display: none;
		}
		input[type=text],
		input[type=email],
		input[type=number],
		select {
			border-radius: 9px;
			border: 2px solid var(--gray-gray-100, #ECEDEE);
			padding: 4px 22px !important;
			@include fig(400, 18, 1.56);
			font-size: 18px !important;
			text-align: center;
			color: #000 !important;
			height: 49px;
			box-shadow: none;
			outline: 0;
			transition: .3s;
			&:active,&:focus {
				border-color: #1B1D22;
			}
			&:hover {
				border-color: #8D95A5;
			}
			@media (min-width: $screen-sm) {
				font-size: 25px !important;
				padding: 3px 22px !important;
			}
			&::placeholder {
				color: #D0CDD5 !important;
			}
		}
		select{
			background: url(data:image/svg+xml;base64,) no-repeat calc(100% - 10px) 50%;
			-moz-appearance: none;
			-webkit-appearance: none;
			appearance: none;
			outline-width: 0;
			&:focus,&:active {
				background: url(data:image/svg+xml;base64,) no-repeat calc(100% - 10px) 50%;
			}
			&:hover{
				background: url(data:image/svg+xml;base64,) no-repeat calc(100% - 10px) 50%;
			}
		}
		.gfield_label, .booking-form__cal-head {
			@include pop(400, 25, 1.24);
			color: #000;
			font-size: 25px !important;
			font-weight: 400 !important;
			text-align: center;
			margin: 0 auto 14px auto !important;
			justify-content: center;
			align-items: center;
			@media (min-width: $screen-sm) {
				font-size: 32px !important;
			}
		}
		.ginput_container_ssa_appointment_field {
			margin-left: -15px;
			margin-right: -15px;
		}
		.gform_wrapper.gravity-theme .gfield input.medium, .gform_wrapper.gravity-theme .gfield select.medium {
			width: 100%;
			@media (min-width: $screen-sm) {
				width: calc(50% - 8px);
			}
		}
		.gfield_required {
			display: none;
		}
		.gform_page_footer {
			padding: 0 !important;
			margin-top: 14px !important;
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 10px;
			flex-direction: column;
			text-align: center;
			@media (min-width: $screen-sm) {
				margin-top: 24px !important;
				flex-direction: row;
				&:after {
					content: '';
					display: none;
					width: 114px;
					height: 18px;
					background: url("/wp-content/themes/acro/images/softphone-form/enter.png") no-repeat center center;
					background-size: contain;
					margin-left: 6px;
				}
			}
			input[type=submit] {
				display: none;
			}
			.button {
				border-radius: 10px;
				background: var(--Primary-500, #6436BF);
				@include pop(500, 20, 1);
				color: #fff !important;
				text-align: center;
				padding: 15px 16px;
				width: 100%;
				cursor: pointer;
				border: none !important;
				transition: .3s;
				line-height: 1 !important;
				margin-bottom: 0 !important;

				@media (min-width: $screen-sm) {
					font-size: 16px;
					padding: 19px 22px;
					width: auto;
				}
				&:hover {
					background: #502b98;
				}
				&.gform_previous_button {
					color: #635E6E !important;
					padding: 0 !important;
					height: auto !important;
					min-height: auto !important;
					background: none !important;
					order: 2;
					@include fig(500, 13, 1.53);
					@media (min-width: $screen-sm) {
						display: none;
					}
					&:hover {
						color: $purple !important;
					}
				}
			}
		}
		.gfield--type-radio {
			.gfield_radio {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				gap: 14px;
				text-align: left;
				@media (min-width: $screen-sm) {
					flex-direction: row;
				}
				.gchoice {
					padding: 22px 24px 22px 83px;
					position: relative;
					box-shadow: 0px 5.983px 15.954px 0px rgba(56, 36, 100, 0.19);
					background: #fff;
					border-radius: 7px;
					transition: .3s;
					width: 100%;
					@media (min-width: $screen-sm) {
						border-radius: 10px;
						opacity: .6;
						padding: 36px 56px 36px 110px;
						width: auto;
					}
					&:hover {
						opacity: 1;
						@media (min-width: $screen-sm) {
							padding-left: 120px;
						}
					}
					&:before {
						content: '';
						display: block;
						width: 38px;
						height: 38px;
						position: absolute;
						left: 24px;
						top: 12px;
						background: url("/wp-content/themes/acro/images/softphone-form/cross-icon.svg") no-repeat center;
						background-size: cover;
						@media (min-width: $screen-sm) {
							width: 50px;
							height: 50px;
							top: 20px;
							left: 46px;
						}
					} 
					.gform-field-label {
						color: #AF1D3A;
						@include pop(400, 19, 1);
						max-width: 100%;
					}
					input {
						opacity: 0;
						position: absolute;
						width: 100%;
						height: 100%;
						top: 0;
						left: 0;
						cursor: pointer;
					}
					&:first-child {
						&:before {
							background-image: url("/wp-content/themes/acro/images/softphone-form/check-icon.svg");
						}
						.gform-field-label {
							color: #148D65;
						}
					}
				}
			}
		}
	}
	&__bottom {
		padding-top: 35px;
		position: relative;
		@media (min-width: $screen-sm) {
			padding-top: 35px;
		}
		.step-1,
