@keyframes acroZoomIn {
	from {
		transform: scale(0);
	}
	to {
		transform: scale(1);
	}
}
@keyframes acroSlideInLeft {
	from {
		transform:translate(-100%, 30px);
	}
	to {
		transform:translate(0, 0);
	}
}
@keyframes acroSlideInRight {
	from {
		transform:translate(100%, 30px);
	}
	to {
		transform:translate(0, 0);
	}
}
@keyframes acroSlideInLeftDown {
	from {
		transform:translate(-100%, -100%);
	}
	to {
		transform:translate(0, -50%);
	}
}
@keyframes acroSlideInRightUp {
	from {
		transform:translate(100%, 100%);
	}
	to {
		transform:translate(0, 30%);
	}
}
@keyframes acroFadeInZoom {
	from {
		transform:scale(0.8);
		opacity: 0;
	}
	to {
		transform:scale(1);
		opacity: 1;
	}
}

@keyframes acroSlideDown {
	from {
		transform:translate(-10%, -50%);
	}
	to {
		transform:translate(0%, 30%);
	}
}
@keyframes acroSlideUp {
	from {
		transform:translate(10%, 50%);
	}
	to {
		transform:translate(0%, -70%);
	}
}

@keyframes acroGrowHorizontal {
	from {
		width: 0;
	}
	to {
		width: 34px;
	}
}
@keyframes acroGrowHorizontalMobile {
	from {
		width: 0;
	}
	to {
		width: 12px;
	}
}